.home{
    overflow-y: scroll;
    overflow: hidden;
    width: 100%;
    
}

.hero{
    height:calc(70vh - 60px);
    background-color: var(---mainColor);
    color: #fff;
    position: relative;
    display: flex;
    flex-direction: column;
     justify-content: center;
     align-items: center;

  }
  .rightSide{
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
  }
.btnHero{
  padding: 10px ;
  margin-top: 15px;
  border-radius: 7px;
  border: none;
  outline: none;
  background-color: solid #fff;
  cursor: pointer;
  font-weight: bold;
  color: var(---mainColor);
}
.btnHero:hover{
  background-color: goldenrod;
  color: white;
  transition-duration: 2s;
  
}

.body h1{
    font-size: 50px;
}
.card{
  
  display:flex;
  flex-direction: column;
  align-items:center;
  
}
.learnCard{
  margin:2rem 0rem;
  display:flex;
  flex-direction: column;
  align-items: center;
  border: none;
}
.learnCard>iframe{
  margin-top:2rem;
  border: none;
}

/* ----beggining services section----*/
.Services{
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 0% 13%;
    margin-top:3rem;
    max-width: 100%;

}
/*services section color pallets*/
.servicesBox{
   display: grid;
   grid-template-columns:repeat(3,1fr);
   gap: 10px;
   width: 100%;
   margin-top:3rem;
}
.box{
 width: 100%;
 display: flex;
 flex-direction: column;
 text-align:center;
 background-color: var(---backGroundColor);
 padding: 2px;
}
.box img{
  width: 100%;
  height:220px;
  margin-bottom: 7px;
  background-position:center;
  background-size:cover;

}
.box h1{
    font-size: 18px;
    margin: 10px 0px;
    font-size:16px ;
    font-weight: 500;
}
.box p{
  font-size: 13px;
  margin-bottom: 5px;
  color:var(---mainTextColor) ;
  
}

/*---end services section----*/


/*BEGIN TIPS CARD-------*/
.tipsCard{
    padding:0% 13%;
    display: flex;
    flex-direction: column;
    text-align:center;
    height:auto;
    width:100%;
    
   
}
.tipsTitle{
  margin-bottom:2rem;
}
.tipsBody{
  display:grid;
  grid-template-columns: repeat(2,1fr);
  
}
.tip{
  background-color: var(---backGroundColor);
  max-width: 450px;
  height:100px;
  display:flex;
  border-radius: 10px;
  margin: 10px;
  
}
.tip img{
  width: 60px;
  height:60px;
  border-radius: 50%;
  margin:10px;
 
}
.tip p{
  font-size:12px;
  margin-top: 25px;
}

/*------END TIPS----*/
/*-------MEDIA SCREEN----*/
@media only screen and (max-width: 600px) {
  .hero{
    margin-bottom: 2rem;
    width: 100%;
    height:60vh;
    overflow: hidden;
    
  }

  
 
  .btnHero{
    padding: 3px 10px;
    font-size:9px;
  }
    .Services{
    display: flex;
    padding: 0;
    
    width: 100%;
    text-align: center; 
    justify-content: center;   
  }
  
.servicesBox{
      display: block;
      padding: 0 15px;
  }
  
  .box{
     width:100%;
     padding: 0px;
     margin-bottom: 2rem;
  }
  

  .learnCard{
    margin-top: 0rem;
    padding: 0px;
    text-align: center;
    align-items: left;
    
  }
  .tipsCard{
    height:100%;
    width:100%;
    margin:0px;
    padding:0px;
  }
  .tipsBody{
    display: flex;
    flex-direction: column;
    
  }
}