.about{
    width: 100%;
    overflow-y: scroll;
    overflow: hidden;
    
}

.aboutContainer{
    padding: 0% 13%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
      
}
.aboutContainer>iframe{
   margin: 2rem 0rem;
   border:none;
   width:700px;
   height:350px;

}
@media only screen and (max-width:600px) {
 .aboutContainer{
    padding:0px 15px;
    margin:0px;
    display:flex;
    justify-content: center;
 }
 .aboutContainer>iframe{
   width:100%;
   height:250px;
   
 }
}