.supportContainer{
    width: 100%;
    overflow-y: scroll;
    overflow: hidden;
}

.support{
    padding: 0% 13%;
    width: 100%;
    min-height:calc(80vh - 60px);
}
.support{
    display: flex;
    flex-direction: column;
    
}
.support h1{
    padding-top: 2rem;
    font-weight: 300;
}
.messageBox{
    display: flex;
    flex-direction: column;
    margin-top: 2rem;
    margin-bottom: 5px;
    width:100%;
}
.textArea{
padding:10px 5px 0px 0px;
height:6rem;
margin-bottom:20px;
width:100%;
max-width: 400px;
outline:none;
}
input{
    margin-bottom: 20px;
    max-width: 400px;
    outline:none;
    border-style:1px;
    padding:10px 5px; 
}


.btn{
    width: 65px;
    padding: 5px 10px;
    background:var(---mainColor);
    border: none;
    border-radius: 5px;
    color: #fff;

}
.btn:hover{
    background-color: var(---btnHover);
    background: transparent;
    border:1px solid var(---mainColor);
    color: var(---mainColor);
}
@media only screen and (max-width:600px) {
 .support{
    padding:0 15px;
    margin:0;
 }
.support>h1{
    font-size:20px;

}
}